
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {API,getQuery} from '../../services'
export default {
data() {
return {
confrim:"",
toast:false,
state:true,
form:{
user_phone:"",
code:"",
user_password:""
},
/*  */
codeText:"",
isDisable:false,
}
},
components: {
XInput,
Group,
XButton,
XHeader,
Toast
},
mounted : function() {
},
methods :{
routerback(){
this.$router.goBack();
},
/*  */
getcode(){
/*  */
if(!this.form.user_phone){
this.confrim="";
this.toast=true;
return false;
}
if(!this.isDisable){
API.login.sendCode({
user_phone:this.form.user_phone,
type:"register"
}).then((res)=>{
if(res.body.code==200){
this.isDisable=true;
this.codeText=60;
let inter=setInterval(()=>{
this.codeText--;
if(this.codeText==1){
clearInterval(inter);
this.isDisable=false;
this.codeText="";
}
},1000);
}else{
this.confrim=res.body.msg;
this.toast=true;
}
});
}
},
/*  */
register(){
/*  */
if(!this.form.user_phone){
this.confrim="";
this.toast=true;
return false;
}
if(!this.form.code){
this.confrim="";
this.toast=true;
return false;
}
if(!this.form.user_password){
this.confrim="";
this.toast=true;
return false;
}
API.login.register(this.form).then((Response)=>{
if(Response.body.code==200){
Response=Response.body.data;
/* vuex */
let token=this.md5(Response.user_id)+Response.user_id;
let userInfo={
loginname : Response.nickname,
avatar : Response.face,
id : Response.user_id,
token : token,
};
localStorage.setItem("userInfo",JSON.stringify(userInfo));
this.$store.dispatch('SetUserInfo',userInfo);
}else{
this.confrim=Response.body.msg;
this.toast=true;
}
});
},

}
}



